import React from 'react'

const Persons = ({ persons, filter, filteredName,deletePerson }) => {
  return (<div>
    {filter ? filteredName.map(person => <div key={person.id}>{person.name} {person.number}</div>) :
      persons.map(person => <div key={person.id}>{person.name} {person.number} <button onClick={() => deletePerson(person.id,person.name)}>delete</button></div>)
    }
  </div>
  )


}

export default Persons;